html,
body,
.App {
  font-family: sans-serif;
  text-align: center;
  background-color: black;
  margin: 0;
  color: white;
}

.star-config {
  max-width: 100vw;
  width: 225px;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
  border-right: solid 2px rgba(255, 255, 255, 0.5);
  -webkit-box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  z-index: 2;
}
.image-holder {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.image-holder div {
  -ms-flex: 1 1;
      flex: 1 1;
}
.image-holder img {
  width: 100%;
}
.image-holder img.active {
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.5);
}

.view-buttons {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 80vw;

  bottom: 20px;
  left: 20px;
  right: 20px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;

  -ms-flex-pack: distribute;

      justify-content: space-around;
}
.view-buttons.top {
  bottom: auto;
  top: 60px;
}
.view-buttons.edit {
  -ms-flex-pack: end;
      justify-content: flex-end;
  bottom: auto;
  left: auto;
  top: 20px;
  right: 20px;
  width: 50%;
}
.view-buttons.edit button {
  -ms-flex: none;
      flex: none;
}
.view-buttons button {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 24px;
  background-color: rgba(0, 0, 0, 0.7);
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  color: white;
  margin: 0 20px;
  -webkit-transition: background-color 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: background-color 0.2s ease, -webkit-box-shadow 0.2s ease;
  -o-transition: background-color 0.2s ease, box-shadow 0.2s ease;
  transition: background-color 0.2s ease, box-shadow 0.2s ease;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, -webkit-box-shadow 0.2s ease;
  -webkit-box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.5);
          box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.5);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.view-buttons button:hover {
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-box-shadow: inset 2px 2px 3px rgba(0, 0, 0, 0.4);
          box-shadow: inset 2px 2px 3px rgba(0, 0, 0, 0.4);
}
.view-buttons button:active,
.view-buttons button.active {
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.4);
          box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.4);
}
.view-buttons button:focus {
  outline: none;
}

.search {
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  pointer-events: none;
}
.search input {
  pointer-events: all;
  margin: 0 auto;
  font-size: 24px;
  background-color: rgba(0, 0, 0, 0.7);
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  color: white;
  margin: 0 20px;
  -webkit-transition: background-color 0.2s ease, border 0.2s ease, -webkit-box-shadow 0.2s ease;
  transition: background-color 0.2s ease, border 0.2s ease, -webkit-box-shadow 0.2s ease;
  -o-transition: background-color 0.2s ease, box-shadow 0.2s ease, border 0.2s ease;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, border 0.2s ease;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, border 0.2s ease, -webkit-box-shadow 0.2s ease;
  -webkit-box-shadow: 0px 0px 0px rgba(255, 255, 255, 0.5);
          box-shadow: 0px 0px 0px rgba(255, 255, 255, 0.5);
  padding: 2px 10px;
}
.search input:focus {
  outline: none;
  border: solid 2px rgba(255, 255, 255, 0.8);
  -webkit-box-shadow: 0px 0px 9px rgba(255, 255, 255, 0.5);
          box-shadow: 0px 0px 9px rgba(255, 255, 255, 0.5);
}



